
"use client";

import { useState, useEffect } from 'react';
import { useParams } from 'next/navigation';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { detailedPatients, Patient, Prescription, prescriptionManager } from "@/lib/constants";
import { Button } from '@/components/ui/button';
import { PlusCircle, FileText, Pill } from 'lucide-react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter, DialogDescription } from "@/components/ui/dialog";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Input } from "@/components/ui/input";

const prescriptionSchema = z.object({
  medicine: z.string().min(2, "Medicine name is required."),
  dosage: z.string().min(1, "Dosage is required."),
});
type PrescriptionFormData = z.infer<typeof prescriptionSchema>;


export default function PatientDetailPage() {
  const params = useParams();
  const patientId = params.id as string;
  
  const [patient, setPatient] = useState<Patient | null>(null);
  const [prescriptions, setPrescriptions] = useState<Prescription[]>([]);
  const [isPrescribeDialogOpen, setPrescribeDialogOpen] = useState(false);
  
  const form = useForm<PrescriptionFormData>({
    resolver: zodResolver(prescriptionSchema),
    defaultValues: { medicine: "", dosage: "" },
  });
  
  useEffect(() => {
    const foundPatient = detailedPatients.find(p => p.id === patientId) || null;
    setPatient(foundPatient);
  }, [patientId]);

  useEffect(() => {
     if (!patient) return;
     
     const handlePrescriptionUpdate = () => {
         const patientPrescriptions = prescriptionManager.getPatientPrescriptions(patient.id);
         setPrescriptions(patientPrescriptions);
     }
     
     handlePrescriptionUpdate(); // Initial load
     
     const unsubscribe = prescriptionManager.subscribe(handlePrescriptionUpdate);
     return () => unsubscribe();
  }, [patient]);


  const handleAddPrescription = (values: PrescriptionFormData) => {
    if (!patient) return;
    prescriptionManager.addPrescription({
        patientName: patient.name,
        medicine: values.medicine,
        dosage: values.dosage,
        doctor: "Dr. Evelyn Reed", // Hardcoded for now
        status: "Pending"
    });
    setPrescribeDialogOpen(false);
    form.reset();
  };

  if (!patient) {
    return (
        <Card>
            <CardHeader><CardTitle>Patient not found</CardTitle></CardHeader>
            <CardContent><p>The requested patient could not be found.</p></CardContent>
        </Card>
    );
  }

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader className="flex flex-row items-center gap-4 space-y-0">
            <Avatar className="w-16 h-16">
              <AvatarImage src={`https://placehold.co/64x64.png?text=${patient.name.charAt(0)}`} alt={patient.name} data-ai-hint="person" />
              <AvatarFallback className="text-2xl">{patient.name.charAt(0)}</AvatarFallback>
            </Avatar>
            <div>
                <CardTitle className="text-3xl">{patient.name}</CardTitle>
                <CardDescription>
                    Condition: <Badge variant={patient.condition === 'Critical' ? 'destructive' : 'secondary'}>{patient.condition}</Badge>
                </CardDescription>
                <p className="text-sm text-muted-foreground">Last Visit: {patient.lastVisit}</p>
            </div>
        </CardHeader>
      </Card>
      
      <Card>
        <CardHeader>
           <div className="flex justify-between items-center">
             <CardTitle className="flex items-center gap-2"><FileText className="w-5 h-5" /> Medical History</CardTitle>
           </div>
        </CardHeader>
        <CardContent>
             <Table>
                <TableHeader>
                    <TableRow>
                        <TableHead>Date</TableHead>
                        <TableHead>Event</TableHead>
                        <TableHead>Details</TableHead>
                         <TableHead>Doctor</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {patient.medicalHistory.map((entry, index) => (
                        <TableRow key={index}>
                            <TableCell>{entry.date}</TableCell>
                            <TableCell>{entry.event}</TableCell>
                            <TableCell className="max-w-xs">{entry.details}</TableCell>
                            <TableCell>{entry.doctor}</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
             </Table>
        </CardContent>
      </Card>
      
       <Card>
        <CardHeader>
           <div className="flex justify-between items-center">
             <CardTitle className="flex items-center gap-2"><Pill className="w-5 h-5" /> Prescriptions</CardTitle>
             <Button onClick={() => setPrescribeDialogOpen(true)}><PlusCircle className="mr-2 h-4 w-4" /> Add Prescription</Button>
           </div>
        </CardHeader>
        <CardContent>
             <Table>
                <TableHeader>
                    <TableRow>
                        <TableHead>Medicine</TableHead>
                        <TableHead>Dosage</TableHead>
                        <TableHead>Doctor</TableHead>
                        <TableHead>Status</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {prescriptions.map((p) => (
                        <TableRow key={p.id}>
                            <TableCell>{p.medicine}</TableCell>
                            <TableCell>{p.dosage}</TableCell>
                            <TableCell>{p.doctor}</TableCell>
                            <TableCell>
                                <Badge variant={
                                    p.status === 'Pending' ? 'secondary' : p.status === 'Unavailable' ? 'destructive' : 'default'
                                } className={p.status === 'Filled' ? 'bg-green-500' : ''}>{p.status}</Badge>
                            </TableCell>
                        </TableRow>
                    ))}
                </TableBody>
             </Table>
        </CardContent>
      </Card>

      <Dialog open={isPrescribeDialogOpen} onOpenChange={setPrescribeDialogOpen}>
        <DialogContent>
            <DialogHeader>
                <DialogTitle>Add New Prescription</DialogTitle>
                <DialogDescription>
                    Prescribe a new medication for {patient.name}.
                </DialogDescription>
            </DialogHeader>
            <Form {...form}>
              <form onSubmit={form.handleSubmit(handleAddPrescription)} className="space-y-4">
                <FormField
                  control={form.control}
                  name="medicine"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Medicine Name</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., Atorvastatin" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="dosage"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Dosage</FormLabel>
                      <FormControl>
                        <Input placeholder="e.g., 20mg" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <DialogFooter>
                  <Button type="button" variant="ghost" onClick={() => setPrescribeDialogOpen(false)}>Cancel</Button>
                  <Button type="submit">Add Prescription</Button>
                </DialogFooter>
              </form>
            </Form>
        </DialogContent>
      </Dialog>
    </div>
  );
}
